ckground-color: #C29B73; }\n      .verifyPhoneWrap .fromBox .nextBtn:active {\n        border-color: #C29B73; }\n","// @import './base.scss';\n$mainColor: #C29B73;\n$subGreenColor: #50A050;\n$subRedColor: #DB2A36;\n$titleColor: #333333;\n$textColor:#666666;\n$textWeakColor:#999999;\n$subTextColor:#eec0c0;\n$tipsColor:#cccccc;\n$lineColor: #ebebeb;\n$borderLineDisabledColor: #eee;\n$bgColor:#f8f8f8;\n$anitColor: #fff;\nbody {\n    font-family: \"Helvetica Neue\", Helvetica, \"PingFang SC\", \"Hiragino Sans GB\", \"Microsoft YaHei\", \"微软雅黑\", Arial, sans-serif;\n}\n\nli {\n    list-style: none;\n}\n\na {\n    text-decoration: none;\n    color: #333333;\n}\n\n@mixin border-top-1px($color) {\n    &:before {\n        border-top: 1px solid $color;\n    }\n}\n\n@mixin border-right-1px($color) {\n    &:before {\n        border-right: 1px solid $color;\n    }\n}\n\n@mixin border-bottom-1px($color) {\n    &:before {\n        border-bottom: 1px solid $color;\n    }\n}\n\n@mixin border-left-1px($color) {\n    &:before {\n        border-left: 1px solid $color;\n    }\n}\n\n@mixin border-1px($color) {\n    position: relative;\n    &:after {\n        content: '';\n        pointer-events: none; //解决iphone上的点击无效Bug\n        position: absolute;\n        left: 0;\n        top: 0;\n        transform-origin: 0 0;\n        border: 1px solid $color;\n    }\n}\n\n//圆角\n@mixin borderRadius($radius) {\n    -webkit-border-radius: $radius;\n    -moz-border-radius: $radius;\n    -ms-border-radius: $radius;\n    -o-border-radius: $radius;\n    border-radius: $radius;\n}\n\n//1px底部边框\n@mixin border-1px($color) {\n    position: relative;\n    &:after {\n        display: block;\n        position: absolute;\n        left: 0;\n        bottom: 0;\n        width: 100%;\n        border-top: 1px solid $color;\n        content: '';\n    }\n}\n\n//定位全屏\n@mixin allcover {\n    position: absolute;\n    top: 0;\n    right: 0;\n}\n\n//定位上下左右居中\n@mixin center {\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n}\n\n//定位上下居中\n@mixin ct {\n    position: absolute;\n    top: 50%;\n    transform: translateY(-50%);\n}\n\n//定位左右居中\n@mixin cl {\n    position: absolute;\n    left: 50%;\n    transform: translateX(-50%);\n}\n\n//宽高\n@mixin wh($width, $height) {\n    width: $width;\n    height: $height;\n}\n\n//字体大小、行高、字体\n@mixin font($size, $line-height, $family: 'Microsoft YaHei') {\n    font: #{$size}/#{$line-height} $family;\n}\n\n//字体大小，颜色\n@mixin sc($size, $color) {\n    font-size: $size;\n    color: $color;\n}\n\n@mixin boxSizing {\n    -webkit-box-sizing: border-box;\n    -moz-box-sizing: border-box;\n    box-sizing: border-box;\n}\n\n//flex 布局和 子元素 对其方式\n@mixin fj($type: space-between) {\n    display: flex;\n    justify-content: $type;\n}\n\n@mixin ellipsis1 {\n    overflow: hidden;\n    white-space: nowrap;\n    text-overflow: ellipsis;\n}\n\n@mixin ellipsis2($num) {\n    display: -webkit-box;\n    overflow: hidden;\n    text-overflow: ellipsis;\n    word-wrap: break-word;\n    white-space: normal !important;\n    -webkit-line-clamp: $num;\n    /*! autoprefixer: off */\n    -webkit-box-orient: vertical;\n}","\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n@import \"../../common/style/mixin.scss\";\n\n.banner {\n  height: 500px;\n  min-width: 1200px;\n  background: url('../../assets/images/register_banner.png') no-repeat center center;\n  background-size: cover;\n}\n\n.verifyPhoneWrap {\n  min-width: 1200px;\n  padding-bottom: 60px;\n  background-color: $bgColor;\n\n  .fromBox {\n    margin: 0 auto;\n    width: 440px;\n    .err {\n      height: 30px;\n      line-height: 30px;\n      font-size: 12px;\n      text-align